body {
  text-align: center;
  font-family: 'Noto Sans',serif; }

a {
  text-decoration: none;
  color: white; }

.header {
  background-color: #AA0601; }

.container {
  max-width: 1300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center; }

@media (max-width: 992px) {
  .container {
    flex-direction: column;
    align-items: center;
    padding-bottom: 0; } }

.container img {
  display: block;
  width: 80px;
  height: 49px; }

@media (max-width: 992px) {
  .container img {
    margin-bottom: 15px; } }

.title-list {
  display: flex;
  align-items: center;
  color: white;
  line-height: 1.5;
  font-size: 20px;
  flex-wrap: wrap; }

@media (max-width: 992px) {
  .title-list li {
    width: 50%;
    height: auto;
    text-align: center; } }

.title-list a {
  padding-left: 64px; }
  .title-list a:hover {
    text-decoration: underline; }

@media (max-width: 992px) {
  .title-list a {
    padding: 10px 62px;
    border: 1px solid white;
    display: block; } }

.footer {
  background-color: #AA0601;
  color: white;
  font-size: 16px;
  line-height: 1.5; }

.footer-container {
  max-width: 1300px;
  margin: 0 auto; }

.footer-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  line-height: 1.5;
  font-size: 20px; }

.footer-title a {
  margin-right: 48px; }
  .footer-title a:hover {
    text-decoration: underline; }

.footer-img img {
  display: block;
  width: 48px;
  height: 48px;
  margin-left: 32px; }

.footer-flex {
  display: flex;
  align-items: center; }

i {
  font-size: 36px;
  margin-right: 22px; }

.footer p {
  font-size: 36px;
  line-height: 1.5; }

.border {
  width: 1296px;
  margin: 0 auto 31px auto;
  border-bottom: 1px solid white; }

.footer-Copyright {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  line-height: 1.5; }

.copyright {
  margin-left: 16px; }
